<template lang="pug">
.page-second-hand-car
    my-page-title(title="I品租购")
    my-car-filter(@new-car="newCar")
    .content(v-loading="loading")
        el-table(:data="list" border style="width: 100%")
            el-table-column(align="center" type="index")
            el-table-column(align="center" label="标题" prop="")
            el-table-column(align="center" label="卖价" prop="")
            el-table-column(align="center" label="新车价/购置税" prop="")
            el-table-column(align="center" label="上牌时间" prop="")
            el-table-column(align="center" label="行驶里程" prop="")
            el-table-column(align="center" label="归属地" prop="")
            el-table-column(align="center" label="操作" prop="")
                template(slot-scope="scope")
                    el-button(type="text") 修改
                    el-button(type="text") 删除
        .pager
            el-pagination(:page-sizes="[20]"
                layout="total,slot,prev,pager,next"
                :current-page.sync="pagination.curPage"
                :page-size="pagination.pageSize"
                :total="pagination.pageTotal"
                @current-change="pageCurPageChange"
                @size-change="pageSizeChange")
</template>

<script>
import pageMixin from '@/mixins/page';
import CarFilter from '../components/CarFilter';
export default {
    mixins: [pageMixin],
    data () {
        return {
            list: [{}],
            loading: false
        };
    },
    components: {
        'my-car-filter': CarFilter
    },
    methods: {
        newCar () {
            this.$router.push({
                path: '/second-hand-car/add'
            });
        }
    }
};
</script>

<style lang="scss" scoped>
.page-second-hand-car {
    .bar {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
}
</style>



